.apicomponent{
    display:flex;
    flex-direction: column;
    align-items: center;
    margin-right:0.5rem;
  }
  .apicomponent :last-child{
    margin-right:0;
  }
    .api{
      margin-top:3rem;
      border: 1px solid #000;
      display: flex;
      margin-bottom: 1rem;
      padding: 1rem;
      background-color: lightgray;
      font-size:large;
      color: black;
    }
  .image{
    width:20rem;
    height: 20rem;
  }
  .input-group-text{
    width:3rem;
    padding:0;
    justify-content: center;
  }
  .statenumber {
    display: grid;
    grid-template-columns: 4.5rem 7rem 4.5rem 7rem;
    margin-top: 3rem;
    margin-bottom: 2rem;
    row-gap: 0.5rem;
    align-items: center;
  }
  .statenumbers{
    text-align:right;
    font-weight: bold;
    margin-right:0.5rem;
  }
  .wetherdiv {
    color: white;
    text-align: center;
    background-color: gray;
    height: 200px;
    width: 500px;
    margin-bottom: 1rem;
  }
  
  .statebody {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
  }
  
  #statecomponent {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .controlcomponent {
    display: flex;
    flex-direction: column;
    align-items: center;
  }